                                <div class="classLabel Class2" v-else-if="item.rating == 4">Class D</div>
                                <div class="classLabel Class3" v-else-if="item.rating == 3">Class C</div>
                                <div class="classLabel Class4" v-else-if="item.rating == 2">Class B</div>
                                <div class="classLabel Class5" v-else-if="item.rating == 1">Class A</div>
                                <div class="videoScript van-multi-ellipsis--l2">{{item.runtime}} min | {{item.genres.join(', ')}} </div>
