                                            <p>Started on {{item.subscription.startTime}}</p>
                                        </div>
                                        <van-progress
                                            style="width: 100%;"
                                            :percentage="(item.subscription.progress/1)/(item.subscription.total/1)*100"
                                            pivot-text=""
