                                            color="linear-gradient(90deg, #746CFF 0%, #C72FFF 36%, #FF34C6 100%)"
                                        />
                                    </div>
                                    <div class='SubscribeBtn' v-if="item.subscription.status == 'in_progress'">Subscribe</div>
