			<van-tabbar-item replace :to="'/Home/'+VHinvite_code" :icon="require('../assets/images/homeNormalIcon.png')">Home</van-tabbar-item>
			<van-tabbar-item replace :to="'/Video/'+VHinvite_code" :icon="require('../assets/images/videoNormalIcon.png')">Video</van-tabbar-item>
			<van-tabbar-item replace :to="'/Libary/'+VHinvite_code" :icon="require('../assets/images/libaryActiveIcon.png')">Library</van-tabbar-item>
			<van-tabbar-item replace :to="'/Team/'+VHinvite_code" :icon="require('../assets/images/teamNormalIcon.png')">Team</van-tabbar-item>
