<script>
	import https from '../api/https'
	export default {
		name: 'Libary',
		data: function(){
			return {
                showLoading: true,
                moneySymbol: https.moneySymbol,
				bgWrapHeight: 200,
                classTypeList: [
                    {
                        id: '',
                        name: 'All'
                    },
                    {
                        id: 1,
                        name: 'Class A'
                    },
                    {
                        id: 2,
                        name: 'Class B'
                    },
                    {
                        id: 3,
                        name: 'Class C'
                    },
                    {
                        id: 4,
                        name: 'Class D'
                    },
                    {
                        id: 5,
                        name: 'Class E'
                    },
                    
                ],
                selectClassTypeId: '',
                NotStartedBox: false,
                SoldOutBox: false,
                subscriptionCount: '',
                // list
                listLoading: false,
                finished: false,
                listData: [],
                page: 1,
                showListNoRecord: false,
                
			}
		},
		created(){
			// this.getData()

            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getData: function(classType){
                clearInterval(this.timer)
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    rating: classType,
                    page: this.page,
                }
				https.fetchGet('/api/library/list',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.listData = this.listData.concat(res.data.list)
                        this.listLoading = false
                        this.page++
                        this.subscriptionCount = res.data.subscriptionCount
                        if(!res.data.hasNext) {
                            this.finished = true
                            console.log('finished')
                        }
                        if(this.listData.length == 0) {
                            this.showListNoRecord = true
                        }
                        this.timer = setInterval(() => {
                            for (let index = 0; index < this.listData.length; index++) {
                                if(this.listData[index].subscription.status == 'not_started'){
                                    let countdown = this.listData[index].subscription.countdown
                                    if(countdown > 0) {
                                        countdown --
                                        this.listData[index].subscription.countdown = countdown
                                    }else {
                                        this.listData[index].subscription.status = 'in_progress'
                                    }
                                }
                            }
                        }, 1000);
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            countdown: function(item){
                if(item.subscription.countdown/60/60/24 > 1) {
                    if(item.subscription.countdown/60/60/24 > 2) {
                        return parseInt(item.subscription.countdown/60/60/24) + ' days'
                    }else {
                        return parseInt(item.subscription.countdown/60/60/24) + ' day'
                    }
                }else {
                    var sec = parseInt(item.subscription.countdown);
                    var min= 0;
                    var hour= 0;
                    if(sec > 60) {
                        min= parseInt(sec/60);
                        sec = parseInt(sec%60);
                        if(min> 60) {
                            hour= parseInt(min/60);
                            min= parseInt(min%60);
                        }
                    }
                    var result = `${hour.toString().padStart(2,'0')}:${min.toString().padStart(2,'0')}:${sec.toString().padStart(2,'0')}`
                    return result;
                }
            },
            selectClassType(item){
                console.log(1)
                if(item.id != this.selectClassTypeId) {
                    this.selectClassTypeId = item.id
                    this.listData = []
                    // this.finished = false
                    this.page = 1
                    this.showListNoRecord = false
                    this.getData(item.id)
                }
            },
            
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		},
       
        destroyed: function(){
            clearInterval(this.timer)
        }
	}
</script>
